.corusel-img {
    height: 450px !important;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    span {
        height: 100%;
        width: 100%;
        display: block;
        background-position: center center;
        background-repeat: no-repeat;
        transition: all .6s ease-out;
        background-size: cover; }
    span:hover {
        transition: all .6s ease-out;
        transform: scale(1.2);
        &:hover:before {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: #4c4c4c;
            opacity: .7; } } }


.slick-prev-corusel {
    background-image: url('../img/back-arrow.png');
    left: 0;
    height: 450px; }

.slick-next-corusel {
    background-image: url('../img/front-arrow.png');
    right: 0;
    height: 450px; }




